class Route {
    _pathname: string;
    _blockClass: any;
    _block: any;
    _props: any;
    __instance: any;

    constructor(pathname: string, view: any, props: any) {
        this._pathname = pathname;
        this._blockClass = view;
        this._block = null;
        this._props = props;
    }
  
    navigate(pathname: string) {
        if (this.match(pathname)) {
            this._pathname = pathname;
            this.render();
        }
    }

    leave() {
        if (this._block) {
            this._block.hide();
        }
    }

    match(pathname: string) {
        return pathname === this._pathname;
    }

    render() {
        if (!this._block) {
            this._block = new this._blockClass(this._props.rootQuery);
            return this._block.render(this._props.rootQuery, this._block);
            // return;
        }

        this._block.render();
    }
}
  
export default class Router {

    routes: Route[];
    history: any
    _currentRoute: Route | null;
    _rootQuery: string;
    __instance: any;

    constructor(rootQuery: string) {
        if (this.__instance) {
            return this.__instance;
        }

        this.routes = [];
        this.history = window.history;
        this._currentRoute = null;
        this._rootQuery = rootQuery;

        this.__instance = this;

        window.addEventListener('popstate', () => console.log('popstate'))
      }

    use(pathname: string, block: any) {
        const route = new Route(pathname, block, {rootQuery: this._rootQuery});

        this.routes.push(route);

        return this;
    }

    start() {
        window.onpopstate = ((event: any) => {
            console.log('event?.currentTarget?.location?.pathname', event?.currentTarget?.location?.pathname)
            this._onRoute(event?.currentTarget?.location?.pathname);
        }).bind(this);

        this._onRoute(window.location.pathname);
    }
  
    _onRoute(pathname: string) {
        console.log('onroute', pathname)
        const route = this.getRoute(pathname);
        console.log('route', route);
        if (!route) {
            return;
        }

        if (this._currentRoute && this._currentRoute !== route) {
            this._currentRoute.leave();
        }

        this._currentRoute = route;
        //   route.render(route, pathname);
        // route.render()
        const content = route.render();
        console.log('content', content);
        const root = document.getElementById(this._rootQuery);
        console.log('root', root);
        if(root) {
            root.innerHTML = content as string;
        }
    }

    go(pathname: string) {
        console.log('11111');
        this.history.pushState({}, '', pathname);
        this._onRoute(pathname);
    }
  
    back() {
        this.history.back();
    }
  
    forward() {
        this.history.forward();
    }
  
    getRoute(pathname: string) {
        return this.routes.find((route: Route) => route.match(pathname));
    }
}
  